.container { position: relative; }
.col-article-primary {
	width: calc(100% - 400px);
}
.article-sidebar {
	width: 380px;
}
.article-title {
	@extend .font-header;
	margin-bottom: $spacer;
	font-size: 3rem;
}

.article-image {
	margin-bottom: $spacer;
	position: relative;
}
.scroll-to-comments {
	position: relative;
	display: inline-block;
	position: absolute;
	right: -4rem; 
	&:after {
		content: 'Kommentarer';
		display: block;
		font-size: 1rem;
		position: absolute;
		bottom: -1rem;
		left: -1rem;
		right: -1rem;
		text-align: center;	
		font-weight: 500;
		color: $col-blue-2;
		text-transform: uppercase;
		@extend .font-g2;
	}
}


.comments-count {
	border-radius: 50%;
	height: 3.5rem;
	width: 3.5rem;
	
	text-align: center;
	line-height: 3.7rem;
	background-color: $col-blue-2;
	display: inline-block;
	
	color: #fff;
	font-size: 2rem;
	font-weight: 300;
	@extend .font-g2;
	overflow: hidden;
	&:hover {
		background-color: darken($col-blue-1, 10%);
	}
	
	i {
		font-style: normal;
		opacity: 0;
		display: block;
		animation: fadeIn .9s;
		animation-fill-mode:forwards;
		animation-delay:0s;
		animation-timing-function: ease-inout;
		transform: translate3d(0, -100%, 0);
	}

	
	
	
}
@keyframes fadeIn {
	0% {
		opacity: 1;
		transform: translate3d(0, -100%, 0);
	}
	75% {
		opacity: 1;
		transform: translate3d(0, 100%, 0);
	}
	75.1% {
		opacity: 0;
		transform: translate3d(0, 100%, 0);
	}
	76% {
		opacity: 1;
		transform: translate3d(0, -100%, 0);
	}

	100% {
		opacity: 1;
		transform: translate3d(0, 0, 0);
	}
}

.star-txt {
	font-size: .8rem;
	@extend .font-g1;
	vertical-align: middle;
}
[class*="article-rating"] {
	font-size: 1.3rem;
	color: #bbb;
}
.article-rating-1 {
	i:nth-child(1) {
		color: $col-blue-1;
	}
}
.article-rating-2 {
	i:nth-child(-n+2) {
		color: $col-blue-1;
	}
}
.article-rating-3 {
	i:nth-child(-n+3) {
		color: $col-blue-1;
	}
}
.article-rating-4 {
	i:nth-child(-n+4) {
		color: $col-blue-1;
	}
}
.article-rating-5 {
	i:nth-child(-n+5) {
		color: $col-blue-1;
	}
}
.article-rating-6 {
	i {
		color: $col-blue-1;
	}
}


.open-gallery {
	display: block;
	position: relative;
}
.gallery-counter {
	position: absolute;
	right: 0;
	bottom: 0;
	background-color: rgba(0,0,0,.8);
	color: #fff;
	text-align: center;
	padding: .625rem;
	font-size: 2rem;
	@extend .font-s2;
	font-weight: 500;
	.show-all {
		display: block;
		font-size: .7rem;
		text-transform: uppercase;
		font-weight: 500;
	}
}
.main-image-caption {
	background-color: rgba(0,0,0,.3);
	padding: .2rem .4rem;
	color: rgba(255,255,255,.9);
	display: inline-block;
	position: absolute;
	top: 0;
	right: 0;
	font-size: .6rem;
	@extend .font-g1;
}

.author-username {
	font-size: .7rem;
	font-weight: 400;
	@extend .font-s2;
	a {
		color: #222;
	}
}

.author-follow {
	display: inline-block;
}
.btn-follow-author {
	padding: .1rem .3rem;
	font-style: normal;
	@extend .font-s2;
	font-weight: 500;
	text-transform: uppercase;
	font-size: .6rem;
	vertical-align: middle;
	display: inline-block;
	background-color: #eee;
	margin-left: .2rem;
	color: #999;
	border-radius: .1rem;
	text-shadow: 1px 1px 0 #fff;
	&:hover {
		background-color: darken(#eee, 5%);
		color: darken(#999, 10%);
	}
}

.btn-follow-author.active {
	padding: 0 .2rem;
	color: #379f1b;
	color: $col-blue-1;
	font-size: .9rem;
	background-color: transparent;
}
.article-content {
	padding-bottom: 10rem;
	
	
}
.article-body {
	@extend .font-g1;
	font-size: 1.1rem;
	&> p {
		@extend .font-g1;
		font-size: 1.1rem;
		line-height: 1.5;
		margin-bottom: $spacer * 2;
		&> a {
			color: $col-blue-1;
			&:hover {
				color: darken($col-blue-1, 10%);
			}
		}
	}
	&>h2 {
		font-weight: 700;
		font-size: 1.2rem
	}

}
.article-byline {
	@extend .font-serif;
	font-size: .9rem;
	@extend .clearfix;
	margin-bottom: $spacer;
	.author-info {
		@extend .font-s2;
		font-weight: 700;
	}
	a {
		color: rgba(0,0,0,.6);
	}
	
}
.article-avatar {
	float: left;
	margin-right: $spacer;
}
.article-author {
	display: inline-block;
	float: left;
	vertical-align: top;
	line-height: 1.4;
	img {
		max-width: 3rem;
		clear: both;
	}
	&+ .article-author {
		margin-left: 2rem; 
	}
}
.author-info {
	display: inline-block;
	margin-top: .625rem;
}
.image-caption {
	font-size: .8rem;
	font-weight: 400;
	color: rgba(0,0,0,.7);
	padding: .4rem 0;
	line-height: 1.4;
	@extend .font-g1;

	
}

.image-meta {
	color: rgba(0,0,0,.4);
	display: block;
	font-size: .6rem;
	@extend .font-s2;
	font-weight: bold;
	text-transform: uppercase;
	padding-top: .4rem;
	.image-count {
		float: right;
	}
}
.article-image-float-right {
	margin-bottom: .12rem;
	margin-left: $spacer;
	position: relative;
	max-width: 37.5%;
	float: right;
	margin-top: .5rem;
}

.article-header {
	margin-bottom: $spacer;	
	border-bottom: $border-width solid $border-color;
}

.article-trumpet {
	display: block;
	font-size: 1.2rem;
	color: $col-blue-1;
}
.article-header-live {
	margin-top: -($spacer * 3);
	padding: $spacer $spacer 0;
	background-color: $background-color;
	margin-bottom: 0;
}

.article-embed-center {
	margin: ($spacer * 2) 0;
	iframe {
		margin: 0 auto;
	}
}

.article-embedded-video {
	margin: ($spacer * 2) 0;
	overflow: hidden;
	* {
		max-width: 100%;
	}
}
.article-summary {
	font-size: 1.4rem;
	color: #666;
	max-width: 930px;
	@extend .font-g1;
	font-weight: 400;
	clear: left;
}


.fact-content {
	font-size: .7rem;
	font-weight: 400;

	@extend .font-g1;

	p, li {
		font-size: .9rem;
		@extend .font-g1;
		line-height: 1.4;
	}
}

.article-timeline-embedded {
	background-color: $col-beige!important;
	
	dl {
		position: relative;
	}
	dt {
		@extend .font-g1;
		font-size: .8rem;
		font-weight: 500;
		display: block;
		background-color: #fff;
		z-index: 2;
		position: relative;
		padding: .625rem 0 .3rem .625rem ;
		color: $col-blue-1;
	}
	dd {
		margin: 0 0 $spacer;
		background-color: #fff;
		z-index: 2;
		position: relative;
		padding: 0 .625rem .625rem ;
		font-size: .9rem;
		position: relative;
		&:after{
			width: 0;
			height: 0;
			content: '';
			border-top: 12px solid #fff;
			border-left: 10px solid transparent;
			border-right: 10px solid transparent;
			position: absolute;
			left: 10px;
			bottom: -8px;
		}	
	}
}

.article-timeline-embedded,
.article-fact-embedded {
	max-width: 37.5%;
	float: right;
	margin: 0 0 $spacer $spacer;
	clear: right;
	padding: $spacer;
	background-color: darken($background-color, 5%);
}



.aside-header {
	@extend .font-sans-alt;
	color: $col-blue-1;
	font-size: 1.5rem;
	text-transform: uppercase;
	font-weight: normal;
	margin-bottom: $spacer;

}


.aside-subheader {
	@extend .font-g2;
	color: #222;
	font-size: 2rem;
	font-weight: 400;
	margin-bottom: .625rem;
	text-transform: uppercase;
	font-weight: 500;
}

.widget-livestream {
	background-color: #fff;
	padding: $spacer;
	border-radius: .2rem;
}



.widget-livestream {
	ul {
		list-style: none;
		position: relative;
		&:before {
			content: '';
			width: .2rem;
			left: 1.3rem;
			top: 0;
			bottom: 0;
			position: absolute;
			background-color: $col-live;
			display: inline-block;
		}
	}
	li {
		@extend .clearfix;
		margin-bottom: $spacer * 2;
		position: relative;
		font-family: georgia;
		font-size: 1.1rem;
		&:before {
			content: '\e815';
			font-family: b-icons;
			float: left;
			margin-left: -1.6rem;
			margin-top: -.0rem;
			color: $col-live;
			background-color: #fff;
			font-size: 1.2rem;
			z-index: 2;
		}
		h2 {
			@extend .font-header;
		}
	}


	time {
		color: $col-live;
		display: block;

	}

	.live-img {
		float: right;
		max-width: 300px;
		margin: 0 0 $spacer $spacer;
		clear: right;
		figcaption {
			font-size: .7rem;
			padding-top: .3rem;
			
		}
	}
	.video-youtube,
	.twitter-tweet {
		margin-left: auto;
		margin-right: auto;
		display: block;
	}	
}


.img-gradient {
	display: inline-block;
	overflow: hidden;
	border-radius: 50%;
	@include gradient-directional( $col-blue-1, #fff, 45deg);

	img {
		filter: grayscale(100%) ;	
		opacity: .7;
	}
}

.article-date {
	@extend .font-serif;
	
	color: #666;
	font-size: .9rem;
	margin-bottom: $spacer;
}



.article-embed-right {
	float: right;
	margin: 0 0 $spacer $spacer;
	max-width: 280px;

}
.embed-header {
	font-family: arial;
}

.article-pull-left {
	margin-left: -$col-1-width;
}

.article-embed-header {
	font-size: .7rem;
	text-transform: uppercase;
	margin-bottom: .2rem;
}
.article-float-left {
	margin-left: -$col-1-width;
	float: left;
	max-width: 200px;
}
.article-embedded-link {	
	
	clear: left;
	width: 100%;
	clear: left;
	padding: 1rem 0 2rem;
	
	.article-body {
		overflow: hidden;
	}
	span {
		font-size: .8rem;
		color: $col-blue-1;
		font-weight: 700;
	}
	figure {
		float: left;
		margin: 0 .625rem 0 0;
	}
	.header {
		@extend .font-g1;
		position: relative;
		
	}
}


$sharebutton: 2.3rem;


[class*="btn-share"] {

	display: inline-block;
	height: $sharebutton;
	width: $sharebutton;
	line-height: $sharebutton;
	background-color: #eee;
	border-radius: 50%;
	text-align: center;
	transition: all .2s ease;
	color: #444;
	i {
		line-height: 1;
		font-size: 1.3rem;
		vertical-align: initial;
	}
	&:hover {
		background-color: darken(#eee, 5%);
		color: #333;
	}
	&:active{
		transform: scale(.95);
	}
}

.btn-share-facebook {
	background-color: $col-facebook;
	color: #fff;
	&:hover {
		background-color: darken($col-facebook, 10%);
		color: #fff;
	}
}
.btn-share-twitter {
	background-color: $col-twitter;
	color: #fff;
	&:hover {
		background-color: darken($col-twitter, 10%);
		color: #fff;
	}
}
.btn-share-linkedin {
	background-color: $col-linkedin;
	color: #fff;
	&:hover {
		background-color: darken($col-linkedin, 10%);
		color: #fff;
	}
}
.social-toggle {
	display: none;
}
.collapsed {
	.extra-buttons {
		display: none;
	}
	.social-toggle {
		display: block;
		&:hover {
			transform: translateY(.3rem);
			color: #666;
		}
	}
}

.btn-label {
	font-size: .6rem;
	color: #ccc;
	padding: .2rem 0 0;
	margin-bottom: -.625rem;
	text-transform: uppercase;
}

.social-toggle {
	display: none;
	padding: .5rem 0;
	transition: all .5s ease;
	margin-top: -$spacer;
	color: #999;
	
}

.share-buttons {
	position: absolute;
	display: inline-block;
	width: $col-1-width;
	text-align: center;
	float: left;
	margin-left: -$col-1-width; 
} 
.share-buttons.fixed {
	position: fixed;
}
.article-image-embed {
	margin-bottom: $spacer;
	margin-left: -$col-1-width;
	img {
		margin-bottom: .625rem;
	}
	figcaption {
		font-size: .7rem;
		color: #999;
	}
}
.article-embed-right {
	margin: 0 0 $spacer $spacer;
	float: right;
}


.byline-blog {
	margin-bottom: $spacer;
	.btn-follow-author {
		margin-left: 0;
	}
	.img-gradient {
		border-radius: 50% 50% 50% 0;
	}
	.author-info {
		@extend .font-s2;
		font-weight: 700;
	}
	
	.article-avatar {
		margin:  0;
		img {
			margin: 0 auto;
		}
	}
}


.article-embed-quote {



blockquote {
	padding: 6rem 0 2rem;
	margin: 0;
	color: #222;
	position: relative;
	&> p {
		@extend .font-header;
		font-size: 1.5rem;
	}
	cite {
		font-style: normal;
		font-size: .8rem;
		line-height: 1.2;
		display: block;
		@extend .font-52;
	}
	&:before {
		position: absolute;
		font-family: b-icons;
		content: '\e816';
		font-size: 4rem;
		top: 0;
	}
}
}

[class*="event-header-"] {
	padding: .425rem .425rem .325rem;
	font-weight: 500;
	@extend .font-g1;
	font-size: 1.5rem;
	text-transform: uppercase;
	line-height: 1;
	display: inline-block;
	background-color: $col-live;
	color: #fff;
}

.event-header-breaking {
	background-color: $col-breaking;
	color: #000;
}
.event-header-live {
	background-color: $col-live;
	color: #fff;
	transform: skew(-10deg,0);
}

.event-header-inawhile {
	color: #fff;
	background-color: $col-blue-2;
}
.event-header-update {
	color: #fff;
	background-color: $col-live;
}

.article-topics {
	font-size: .75rem;
	@extend .font-g1;
	color: #999;
	a {
		display: inline-block;
		border-radius: 40rem;
		vertical-align: middle;
		padding: .3rem .625rem .2rem; 
		margin-left: .3rem;
		background-color: rgba(0,0,0,.05);
		color: #666;
		&:hover {
			background-color: rgba(0,0,0,.1);
			text-decoration: none;
		}
	}
}

.article-image-longread {
	background-size: cover;
	background-position: center center;
	height: 40rem;
	margin-top: -$spacer;
	position: relative;
	margin-bottom: 2 * $spacer;
	.article-badge {
		color: #fff;
		padding-bottom: $spacer;
		border-bottom: $border-width solid rgba(255,255,255,.5);
	}
	&:after {
		position: absolute;
		left: 0;
		right: 0;
		top: 0;
		bottom: 0;
		background-color: rgba(0,0,0,.4);
		z-index: 10;
		content: '';
	}
	.article-header {
		position: absolute;
		top: 0;
		left: .625rem * 2;
		right: .625rem * 2;
		z-index: 100;
		border: 0;
		padding-top: 1rem;
	}
	.article-title {
		color: #fff;
		font-size: 4rem;
		max-width: 45rem;
	}
	


}

.tpl-article-longread {
	.article-byline {
		text-align: center;
		margin-bottom: 2 * $spacer;
	}
	.article-date {
		text-align: center;	
		margin-bottom: 2 * $spacer;
	}
	.article-author {
		float: none;
		text-align: left;

	}
	.article-summary {
		text-align: center;
		margin-left: auto;
		margin-right: auto;
		margin-bottom: 2 * $spacer;
	}
	.article-body {
		padding-left: 12rem;
		padding-right: 12rem;
	}
	.article-image-embed-longread {
		margin: (2 * $spacer) -5rem;
	}
}